import { NextResponse } from "next/server";
import { db } from "@/drizzle/db";
import {
  user,
  techStack,
  stackTechnologyItem,
  account,
} from "@/drizzle/db/schema";
import { eq, asc, and, isNotNull } from "drizzle-orm";

const STRIPE_CARD_ID = "internal_stripe_card"; // Assurez-vous que c'est le même ID qu'ailleurs

interface ApiTechItemForProfile {
  // Similaire à ApiTechItem mais pour ce contexte
  id: string;
  name: string;
  color: string;
  technologyId: string;
  category: string;
  gridCols: number;
  gridRows: number;
  isProject?: boolean;
  favicon?: string;
  url?: string;
  description?: string;
  order?: number;
  stars?: number;
  forks?: number;
  mrr?: number; // Ajout pour le MRR
  mrrCurrency?: string; // Ajout pour la devise du MRR
  // Champs spécifiques à Stripe, si nécessaire pour l'affichage (normalement pas, car StripeCard s'en occupe)
  // isStripeCard?: boolean; // Peut être utile pour le frontend pour le rendu conditionnel si TechDisplayCard est utilisé
}

export async function GET(
  request: Request,
  { params }: { params: { userId: string } }
) {
  const userId = params.userId;

  if (!userId) {
    return NextResponse.json({ error: "User ID is required" }, { status: 400 });
  }

  try {
    // 1. Récupérer les informations de l'utilisateur
    const userInfoFromDb = await db.query.user.findFirst({
      where: eq(user.id, userId),
      columns: {
        id: true,
        name: true,
        email: true, // Optionnel: décidez si l'email doit être public
        image: true,
        description: true,
        createdAt: true,
        layoutConfig: true,
        shareCount: true,
      },
    });

    if (!userInfoFromDb) {
      return NextResponse.json({ error: "User not found" }, { status: 404 });
    }

    // 2. Vérifier la connexion Stripe pour cet utilisateur
    const stripeConnection = await db.query.account.findFirst({
      where: and(
        eq(account.userId, userId),
        eq(account.providerId, "stripe"),
        isNotNull(account.accessToken)
      ),
      columns: {
        id: true, // Juste besoin de savoir si un enregistrement existe
      },
    });

    const hasStripeConnection = !!stripeConnection;

    // Combiner les informations utilisateur avec le statut de connexion Stripe
    const userInfo = {
      ...userInfoFromDb,
      hasStripeConnection,
    };

    // 3. Récupérer les stacks technologiques de l'utilisateur
    const userStacksFromDb = await db.query.techStack.findMany({
      where: eq(techStack.userId, userId),
      with: {
        technologies: {
          columns: {
            id: true, // C'est l'ID de stackTechnologyItem
            name: true,
            color: true,
            technologyId: true,
            category: true,
            gridCols: true,
            gridRows: true,
            url: true,
            isProject: true,
            description: true,
            favicon: true,
            order: true,
          },
          orderBy: (stackTechnologyItem, { asc }) => [
            asc(stackTechnologyItem.order),
          ],
        },
      },
      orderBy: (techStack, { asc }) => [asc(techStack.createdAt)],
    });

    // Injecter la carte Stripe si nécessaire
    const userStacksWithPossibleStripeCard = await Promise.all(
      userStacksFromDb.map(async (stack) => {
        let technologiesWithStripe: ApiTechItemForProfile[] = (
          stack.technologies as any[]
        ).map((tech) => ({
          ...tech,
          id: String(tech.id),
        }));

        if (stack.showStripeCard) {
          let mrrData = { total: 0, currency: "USD" };
          if (hasStripeConnection) {
            // Vérifier si l'utilisateur du profil a une connexion Stripe active
            try {
              // Note: fetch direct à une autre de nos API. Assurez-vous que l'URL est correcte
              // et que la variable d'environnement NEXT_PUBLIC_APP_URL ou similaire est définie
              const appUrl =
                process.env.NEXT_PUBLIC_APP_URL || "http://localhost:3000";
              const mrrResponse = await fetch(
                `${appUrl}/api/stripe/mrr/${userId}`
              );
              if (mrrResponse.ok) {
                const mrrResult = await mrrResponse.json();
                mrrData.total = mrrResult.total;
                mrrData.currency = mrrResult.currency;
              } else {
                console.warn(
                  `[API Profile GET] Failed to fetch MRR for user ${userId}: ${mrrResponse.status}`
                );
              }
            } catch (mrrError) {
              console.error(
                `[API Profile GET] Error fetching MRR for user ${userId}:`,
                mrrError
              );
            }
          }

          const stripeTechItem: ApiTechItemForProfile = {
            id: STRIPE_CARD_ID,
            name: "Stripe MRR",
            color: "#635BFF",
            technologyId: STRIPE_CARD_ID,
            category: "Services",
            gridCols: stack.stripeCardColSpan || 1,
            gridRows: stack.stripeCardRowSpan || 1,
            isProject: false,
            order:
              stack.stripeCardOrder === null ||
              stack.stripeCardOrder === undefined
                ? -1
                : stack.stripeCardOrder,
            mrr: mrrData.total,
            mrrCurrency: mrrData.currency,
          };
          technologiesWithStripe.push(stripeTechItem);
        }
        technologiesWithStripe.sort(
          (a, b) => (a.order ?? Infinity) - (b.order ?? Infinity)
        );
        return {
          ...stack,
          technologies: technologiesWithStripe,
        };
      })
    );

    // Enrichir les technologies avec les données GitHub si applicable
    const processedStacks = await Promise.all(
      userStacksWithPossibleStripeCard.map(async (stack) => {
        const processedTechnologies = await Promise.all(
          (stack.technologies || []).map(
            async (techItem: ApiTechItemForProfile) => {
              // S'assurer que techItem.id est une string ici aussi si on ne le fait pas avant
              // Si l'item est la carte Stripe, on ne veut pas la traiter comme un projet GitHub
              if (techItem.id === STRIPE_CARD_ID) {
                return {
                  ...techItem,
                  isProject: techItem.isProject || false,
                  url: techItem.url || undefined,
                  description: techItem.description || undefined,
                  favicon: techItem.favicon || undefined,
                  stars: techItem.stars || undefined,
                  forks: techItem.forks || undefined,
                  // mrr et mrrCurrency sont déjà sur techItem s'il s'agit de la carte Stripe
                };
              }

              if (
                techItem.isProject &&
                techItem.url &&
                typeof techItem.url === "string" &&
                techItem.url.startsWith("https://github.com/")
              ) {
                try {
                  const match = techItem.url.match(
                    /github\.com\/([^/]+)\/([^/]+)/
                  );
                  if (match) {
                    const owner = match[1];
                    const repoName = match[2].replace(/\.git$/, "");

                    const githubRes = await fetch(
                      `https://api.github.com/repos/${owner}/${repoName}`,
                      {}
                    );

                    if (githubRes.ok) {
                      const githubData = await githubRes.json();
                      return {
                        ...techItem,
                        description:
                          githubData.description || techItem.description,
                        stars: githubData.stargazers_count,
                        forks: githubData.forks_count,
                      };
                    } else {
                      console.warn(
                        `Failed to fetch GitHub data for ${techItem.url}: ${
                          githubRes.status
                        } ${await githubRes.text()}`
                      );
                    }
                  }
                } catch (ghError) {
                  console.error(
                    `Error fetching GitHub data for ${techItem.url}:`,
                    ghError
                  );
                }
              }
              return {
                ...techItem,
                isProject: techItem.isProject || false,
                url: techItem.url || undefined,
                description: techItem.description || undefined,
                favicon: techItem.favicon || undefined,
                stars: techItem.stars || undefined,
                forks: techItem.forks || undefined,
              };
            }
          )
        );
        return { ...stack, technologies: processedTechnologies };
      })
    );

    return NextResponse.json({
      user: userInfo,
      stacks: processedStacks,
    });
  } catch (error) {
    console.error(`Error fetching profile data for user ${userId}:`, error);
    return NextResponse.json(
      { error: "Failed to fetch profile data" },
      { status: 500 }
    );
  }
}
